










































import Tools from '@/utils/Tools';
import { Component, Prop, Vue } from 'vue-property-decorator';
import { Action } from 'vuex-class';

@Component({
  components: {},
})
export default class Layout extends Vue {
  @Action('login/LOGINOUT') private LOGINOUT: any;
  public headerImg: any = require('@/assets/img/small-logo.png');
  @Prop() changeCollapse: any;

  public handleCommand(command: string) {
    if (command === 'loginOut') {
      this.loginOut();
    }
  }

  public loginOut() {
    this.LOGINOUT({}).then((res: any) => {
      Tools.backupLogin();
    });
  }

  public changeCollapseClick() {
    this.changeCollapse();
  }
}
