<script>

  export default {
    props: {
      appMain: Object,
    },
    created() {
      this.getBreadcrumb();
    },
    data() {
      return {
        levelList: null,
        nowList: {},
      };
    },
    watch: {
      $route() {
        this.getBreadcrumb();
      },
    },
    methods: {
      getBreadcrumb() {
        console.log(this.$route);
        let matched = this.$route.matched.filter(item => item.name && !item.meta.hidden);
        this.levelList = matched;
        this.nowList = this.$route;
      },
    },
  };
</script>

