





















































import AppMain from '@/components/layout/AppMain/index.vue';
import { Component, Vue } from 'vue-property-decorator';
import Breadcrumb from './Breadcrumb/index.vue';
import AppHeader from './header/index.vue';
import Menus from './menus/index.vue';
import menu from './menus/menu';


@Component({
  components: {
    AppMain,
    AppHeader,
    Menus,
    Breadcrumb,
  },
})
export default class Layout extends Vue {
  private appMain: object = {};
  private menuList: any[] = menu;
  public isCollapse: boolean = false;

  public changeCollapse() {
    this.isCollapse = !this.isCollapse;
    if (document.documentElement.clientWidth > 991) {
      this.isCollapse ? document.getElementsByTagName('body')[0].className = 'nav_small' :
        document.body.removeAttribute('class');
    } else {
      this.isCollapse ? document.getElementsByTagName('body')[0].className = 'mobile_nav' :
        document.body.removeAttribute('class');
    }
  }
}
